code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0; }

ul {
  list-style-type: none;
  padding: 0;
  margin: 0; }

* {
  box-sizing: border-box;
  text-decoration: none; }

html {
  color: #141414;
  font-family: sans_pro, sans-serif, Arial, serif;
  font-size: 16px; }

body {
  margin: auto;
  font-size: 1rem;
  height: 100%;
  background-color: #f8f8f1; }

.App {
  text-align: center;
  max-width: 100%; }

.route {
  height: 100vh; }

@font-face {
  font-family: sans_pro;
  src: url(/device-manager/static/media/SourceSansPro-Regular.3f482321.ttf); }

@font-face {
  font-family: sans_pro_light;
  src: url(/device-manager/static/media/SourceSansPro-Light.3ba9836f.ttf); }

@font-face {
  font-family: sans_pro_bold;
  src: url(/device-manager/static/media/SourceSansPro-Bold.7e0ee8c4.ttf); }

.home h1 {
  height: 2.5rem; }

/********** Cmps **********/
.main {
  height: calc(100vh - 2.5rem);
  display: flex;
  flex-direction: column; }
  .main .bar-device {
    position: relative;
    min-height: 2rem; }
    .main .bar-device h2 {
      height: 2rem; }
    .main .bar-device .only-mobile .mask-layer {
      z-index: 1000000000;
      position: fixed;
      height: 100%;
      width: 100%;
      background-color: rgba(95, 70, 45, 0.05);
      top: 0;
      left: 0; }
    .main .bar-device .only-mobile button {
      position: absolute;
      left: 0;
      top: 0;
      height: 100%; }
  .main .preview-device {
    border-top: 1px solid #969696;
    height: 100%; }

.main-preview {
  height: 100%;
  background-color: #cdc9e8;
  flex-grow: 3;
  display: flex;
  flex-direction: column; }
  .main-preview .device-catagory {
    min-height: 36px; }
    .main-preview .device-catagory span {
      cursor: pointer;
      background-color: blanchedalmond;
      border-bottom: 1px solid #969696;
      padding: 9px 0 6px 0;
      width: 33.333333%;
      min-width: 130px;
      flex-grow: 1; }
      .main-preview .device-catagory span:hover {
        background-color: #ffc685; }
  .main-preview .playground {
    height: 100%;
    position: relative; }
    .main-preview .playground .wrap-device {
      /* Disables pull-to-refresh but allows overscroll glow effects. */
      overscroll-behavior-y: contain;
      position: absolute; }
    .main-preview .playground .device-icon {
      /* Disables pull-to-refresh but allows overscroll glow effects. */
      overscroll-behavior-y: contain;
      position: relative;
      height: 100px;
      width: 100px;
      padding: 4px;
      border-radius: 4px;
      -webkit-transition: background-color 0.3s;
      transition: background-color 0.3s; }
      .main-preview .playground .device-icon:hover {
        background-color: rgba(255, 212, 189, 0.5); }
      .main-preview .playground .device-icon img {
        height: 100%; }
    .main-preview .playground svg {
      position: absolute; }
      .main-preview .playground svg #connection {
        stroke-width: 1.5px;
        stroke: red; }

.device-list {
  flex-grow: 1;
  max-width: 220px;
  min-width: 140px;
  overflow: auto;
  border-right: 1px solid #969696;
  z-index: 1000000001; }
  .device-list .single-device {
    background-color: #ddebf1;
    cursor: pointer;
    padding: 8px 0;
    border-bottom: 1px solid #969696; }
    .device-list .single-device:hover {
      background-color: #c3e7f8; }
    .device-list .single-device.selected {
      background-color: #a4d2e7; }
  .device-list.mobile {
    position: absolute;
    top: 100%;
    left: 0;
    border: 1px solid #969696;
    box-shadow: 0px 0px 2px 0px #969696;
    min-width: 60vw;
    width: 250px;
    max-width: 75vw;
    max-height: calc(100vh - 9rem);
    border-radius: 2px; }

/************************** Tablet and Desktop **************************/
@media (min-width: 64px) and (max-width: 279px) {
  html {
    font-size: 10px; } }

@media (min-width: 280px) and (max-width: 479px) {
  html {
    font-size: 14px; } }

@media (min-width: 480px) and (max-width: 767px) {
  html {
    font-size: 16px; } }

@media (min-width: 1024px) and (max-width: 1699px) {
  html {
    font-size: 18px; } }

@media (min-width: 1700px) and (max-width: 1919px) {
  html {
    font-size: 20px; } }

@media (min-width: 1920px) and (max-width: 2399px) {
  html {
    font-size: 22px; } }

@media (min-width: 2400px) {
  html {
    font-size: 24px; } }

/**************** Helpers ****************/
/********** Flex **********/
.flex {
  display: flex; }

.space-between {
  justify-content: space-between; }

.space-even {
  justify-content: space-evenly; }

.space-around {
  justify-content: space-around; }

.space-center {
  justify-content: center; }

.align-center {
  align-items: center; }

.flex.wrap {
  flex-wrap: wrap; }

.flex-col {
  display: flex;
  flex-direction: column; }

/********** Lists **********/
.clean-list {
  list-style-type: none;
  margin: 0;
  padding: 0; }

.align-left {
  text-align: left; }

.text-wrap {
  overflow-wrap: break-word;
  word-wrap: break-word;
  -webkit-hyphens: auto;
      -ms-hyphens: auto;
          hyphens: auto; }

